<template>
  <div>
    <TypeNav />
    <ListContainer />
    <TodayRecommend />
    <Rank />
    <Like />
    <Floor v-for="fool in foolList" :key="fool.id" :fool="fool" />
    <Brand />
  </div>
</template>

<script>
import Brand from './components/Brand'
import Floor from './components/Floor'
import Like from './components/Like'
import ListContainer from './components/ListContainer'
import Rank from './components/Rank'
import TodayRecommend from './components/TodayRecommend'
import { getFoolList } from '@/api'
export default {
  name: 'Home',
  components: {
    Brand,
    Floor,
    Like,
    ListContainer,
    Rank,
    TodayRecommend,
  },
  data() {
    return {
      foolList: [],
    }
  },
  mounted() {
    this.foolListget()
  },
  methods: {
    async foolListget() {
      let re = await getFoolList()
      this.foolList = re
      console.log(re)
    },
  },
}
</script>

